import React from 'react'
import { TimelineItem } from 'vertical-timeline-component-for-react'
import { Carousel } from 'react-bootstrap'
const ProjectSingkat = ({ project, toggleProjectDetail }) => {
	const waktu = `${project.tanggal_selesai}`

	return (
		<TimelineItem
			key="001"
			dateText={waktu}
    		style={{ color: '#0d6efd' }}
			dateInnerStyle={{ background: '#0d6efd', color: '#fff'}}
		>
			<h3 className='fw-bolder'>{project.nama_project}</h3>
			<h6 className='mb-3 mt-2'>{project.subtitle}</h6>
			{/* <p>{project.deskripsi}</p> */}
			<Carousel className="shadow">
				{
					project.gambar.map((link, index) => (
						<Carousel.Item key={index} className="shadow-lg">
							{/* <p>{link}</p> */}
							<img src={require(""+link+"")} alt={project.nama_project} style={{ width:"100%", height:"auto", marginBottom: "1rem" }}/>
						</Carousel.Item>
					))
				}
			</Carousel>
			
			{/* <Link to={project.slug} onClick={toggleProjectDetail} className="btn btn-outline-primary w-100">Read more</Link> */}
		</TimelineItem>
	)
}


export default ProjectSingkat