import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInstagram, faTwitter, faLinkedin, faFacebook } from '@fortawesome/free-brands-svg-icons';
import { Container } from 'react-bootstrap';
const Footer = () => {
  return (
    <div className="px-4 pt-4">
				<footer className='border border-dark p-5 bg-black d-flex justify-content-center align-items-center'>
					<div className='w-75'>
						<div className='mt-4'>
							<h4 className='text-white'>Daniel Kurniawan</h4>
							<p className='m-0 text-white'>Mail me : <span className="text-primary">danielkurniawanbs@gmail.com</span></p>
						</div>
						<Container fluid className='mt-4'>
							<p className='text-center text-white fw-lighter'>© 2021 Daniel Kurniawan - All rights reserved</p>
							<a href="https://www.instagram.com/daxcella/" target={"_blank"} rel="noreferrer" className='px-2 text-white'><FontAwesomeIcon icon={faInstagram}/></a>
							<a href="https://twitter.com/daxcella" target={"_blank"} rel="noreferrer" className='px-2 text-white'><FontAwesomeIcon icon={faTwitter}/></a>
							<a href="https://www.linkedin.com/in/daniel-axcella-kurniawan/" target={"_blank"} rel="noreferrer" className='px-2 text-white'><FontAwesomeIcon icon={faLinkedin}/></a>
							<a href="https://www.facebook.com/daniel.axcella/" target={"_blank"} rel="noreferrer" className='px-2 text-white'><FontAwesomeIcon icon={faFacebook}/></a>
							<br />
							{/* <a href='#' className="text-white">About this website</a> */}
						</Container>
					</div>
				</footer>
			</div>
  )
}

export default Footer