import './projects.css'
import React from 'react'
import ProjectSingkat from './ProjectSingkat'
import { Timeline } from 'vertical-timeline-component-for-react'
import { Outlet } from 'react-router-dom'
import Footer from '../home/footer'
import { useState } from 'react'
const Projects = ({projects}) => {
	const [UIState, setUI] = useState(false);
	const changeUI = () => {
		setUI(!UIState)
	}

	return (
		<>
			{UIState ? 
				<Outlet/> 
				
				: 
				
				<div>
					<div className="bg-projects d-flex justify-content-center align-items-center p-5 min-vh-50">
						<h1 className='fw-bolder text-white'>Projects</h1>
					</div>
					<Timeline lineColor={'#ddd'}>
						{projects.map((project) => (
							<ProjectSingkat key={project.id} project={project} toggleProjectDetail={changeUI}/>
						))}
					</Timeline>

				</div>
			}

			
			<Footer/>
		</>
	)
}

export default Projects