import React from 'react'
import './login.css'
import { Form, Button } from 'react-bootstrap'
import {  useState } from 'react'
import axios from 'axios'

const Login = () => {

	// state for email + password
	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("")
	const onLoginSubmit = async (e) => {
		e.preventDefault();
		alert(email + ", " + password)
		axios.post('http://127.0.0.1:8000/api/login', {
			'email' : email,
			'password' : password,
		})
		.then((response) => {
			alert("Login")
		})
		.catch((error) => {
			alert("Error")
		});
	};
	const resetForm = () => {
		alert("reset")
		setEmail("");
		setPassword("");
	}
	return (
		<div className='vh-100 p-5 d-flex justify-content-center align-items-center'>
			<div className=''>
				<h6 className='text-start'>Daniel <span className="text-primary">Kurniawan</span></h6>
				<h2 className='fw-bolder text-start'>Admin Login.</h2>
				<form onSubmit={onLoginSubmit}>
					<div className="py-3">
						<Form.Group className="mb-3" controlId="formBasicEmail">
							<Form.Label className="text-start d-block">Email</Form.Label>
							<Form.Control type="email" placeholder="example@domain.com" onChange={(e) => setEmail(e.target.value)} required/>
							<Form.Text className="text-muted text-start">
								Please, never share your email with anyone else.
							</Form.Text>
						</Form.Group>

						<Form.Group className="mb-3" controlId="formBasicPassword">
							<Form.Label className="text-start d-block">Password</Form.Label>
							<Form.Control type="password" placeholder="10+ character" onChange={(e) => setPassword(e.target.value)} required/>
						</Form.Group>
					</div>
					<div className='d-flex align-items-start'>
						<Button variant="primary" type="submit" className='me-1'>
							Submit
						</Button>
						<Button variant="outline-primary-no-border" onClick={resetForm} className='mx-2'>
							Reset
						</Button>
					</div>
				</form>
			</div>
		</div>
	)
}

export default Login