import './home.css'
import { Row, Col} from 'react-bootstrap';
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import TypeAnimation from 'react-type-animation';
import Footer from '../home/footer';
import { Fade, Zoom } from 'react-reveal';
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import HomeEducation from './home-education';

export default class Home extends Component {
	render() {
		return (
			<div>
				<Fade>
					<div className="vh-100">
						<div className='h-100 container-fluid'>
							<Row className='h-100'>
								<Col lg="12" className="d-flex justify-content-center align-items-center">
									<div className='container-fluid mx-5'>
									<TypeAnimation cursor={true}
										sequence={[
												'Salam Kenal !',2000, 
												'Hello and Welcome', 2000, 
												'Sugeng Rawuh',2000,''
										]}
										wrapper="h1"
										repeat={Infinity}
										className='text-dark text-start fw-bolder text-center'
									/>
										<p className='text-center my-4'>My name is Daniel Axcella Kurniawan, and welcome to my portfolio. In this website, I will be giving you a brief tour about my professional and personal career. Hope you like it !</p>
										<Link to="/projects" className='btn btn-outline-primary'>My Projects</Link>
									</div>
								</Col>
								
							</Row>
						</div>
					</div>
				</Fade>
					
				<div className="min-vh-100 p-5">
					<Fade left cascade>
						<div className="container-fluid">
							<Row>
								<Col lg="6" className='p-3'>
									<img src={require("./imgs/user-cartoon.png")} alt="Vue" id='f1' style={{ width:"60%", height:"auto" }}/>
								</Col>
								<Col lg="6" className='d-flex justify-content-center align-items-center'>
									<div>
										<h2 className="fw-bolder text-start mb-4">About me.</h2>
										<p className='text-start'>Seorang programmer yang bertekun di bidang web development. Saya tertarik pada full-stack development, khususnya di bidang Web Development. Secara profesional, saya membantu Kantor Sistem Informasi Universitas Atma Jaya Yogyakarta untuk membuat sebuah sistem pengelolaan presensi mahasiswa, dan merancang beberapa tema milik situs universitas yang dapat anda lihat pada halaman <strong>Projects</strong>.</p>
									</div>
								</Col>
							</Row>
						</div>


						<div className="container-fluid mt-4">
							<div className="row">
								<div className="col-lg">
									<h2 className='text-start fw-bold'>My Speciality.</h2>
									<p className='text-start my-0'>Saya memiliki ketertarikan khusus terhadap pemrograman berbasis web. Selama perkuliahan berlangsung, saya mempelajari beberapa <i>framework</i> seperti Laravel, Vue, React, dan .NET <i>Framework</i>. Berikut merupakan <i>framework</i> yang saya pelajari dan gunakan di dalam keseharian pembuatan <i>project</i>.</p>
									<div className="row mt-3">
										<div className="col-lg p-3 d-flex justify-content-center align-items-center">
											<div>
												<img src={require("./imgs/vue.png")} alt="Vue" id='f1' style={{ width:"39%", height:"auto" }}/>
												<p className='mb-0 mt-2'>Vue</p>
											</div>
										</div>
										<div className="col-lg p-3 d-flex justify-content-center align-items-center">
											<div>
												<img src={require("./imgs/laravel.png")} alt="Vue" id='f1' style={{ width:"35%", height:"auto" }}/>
												<p className='mb-0 mt-2'>Laravel</p>
											</div>
										</div>
										<div className="col-lg p-3 d-flex justify-content-center align-items-center">
											<div>
												<img src={require("./imgs/mysql.png")} alt="Vue" id='f1' style={{ width:"55%", height:"auto" }}/>
												<p className='mb-0 mt-2'>MySQL</p>
											</div>
										</div>
									</div>
								</div>
								<div className="col-lg">
									<h2 className='text-start fw-bold'>My Interests.</h2>
									<p className='text-start my-0'>Selain <i>skill</i> yang sudah saya miliki, saya ingin terus belajar lebih jauh dalam bidang <i>web programming</i>. Berikut merupakan <i>framework</i> yang menarik perhatian saya untuk dipelajari lebih dalam.</p>
									<div className="row mt-3">
										<div className="col-lg p-3 d-flex justify-content-center align-items-center">
											<div>
												<img src={require("./imgs/aspnet.png")} alt="Vue" id='f1' style={{ width:"40%", height:"auto" }}/>
												<p className='mb-0 mt-2'>.NET</p>
											</div>
										</div>
										<div className="col-lg p-3 d-flex justify-content-center align-items-center">
											<div>
												<img src={require("./imgs/database.png")} alt="Vue" id='f1' style={{ width:"30%", height:"auto" }}/>
												<p className='mb-0 mt-2'>SQL Server</p>
											</div>
										</div>
										<div className="col-lg p-3 d-flex justify-content-center align-items-center">
											<div>
												<img src={require("./imgs/flutter-original.png")} alt="Vue" id='f1' style={{ width:"40%", height:"auto" }}/>
												<p className='mb-0 mt-2'>Flutter</p>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</Fade>

					<div className="bg-image-blue mt-4">
						<HomeEducation title={"Career"} description={"Berikut merupakan sejarah karir profesional yang pernah saya jalani."}/>
					</div>
					<div className="min-vh-100">
						<VerticalTimeline lineColor='#000'>
							<VerticalTimelineElement
								className="vertical-timeline-element--work"
								
								
								date="13 Agustus 2018"
								iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
								// icon={<WorkIcon />}
							>
								<h3 className="vertical-timeline-element-title fw-bolder">Universitas Atma Jaya Yogyakarta</h3>
								<h6 className="vertical-timeline-element-subtitle mt-1">Daerah Istimewa Yogyakarta, ID</h6>
								<p className='text-start'>
									Terdaftar sebagai mahasiwa Universitas Atma Jaya Yogyakarta dalam program studi Informatika.
								</p>
							</VerticalTimelineElement>
							<VerticalTimelineElement
								className="vertical-timeline-element--work"
								date="Agustus 2020 - Desember 2020"
								iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
								// icon={<WorkIcon />}
							>
								<h3 className="vertical-timeline-element-title fw-bolder">Asisten Dosen</h3>
								<h6 className="vertical-timeline-element-subtitle mt-1">Universitas Atma Jaya Yogyakarta</h6>
								<p className='text-start'>
									Asisten dosen dalam mata kuliah Pemrograman Berorientasi Objek 
								</p>
							</VerticalTimelineElement>
							<VerticalTimelineElement
								className="vertical-timeline-element--work"
								date="Juni 2021 - Januari 2022"
								iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
								// icon={<WorkIcon />}
							>
								<h3 className="vertical-timeline-element-title fw-bolder">Web Developer</h3>
								<h6 className="vertical-timeline-element-subtitle mt-1">Kantor Sistem Informasi Universitas Atma Jaya Yogyakarta</h6>
								<p className='text-start'>
									Mendesain sistem pengelolaan presensi mahasiswa bagi karyawan tetap universitas, merancang tema unit dalam WordPress.
								</p>
							</VerticalTimelineElement>
							<VerticalTimelineElement
								className="vertical-timeline-element--work"
								date="Desember 2021 - Present"
								iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
								// icon={<WorkIcon />}
							>
								<h3 className="vertical-timeline-element-title fw-bolder">Web Developer</h3>
								<h6 className="vertical-timeline-element-subtitle mt-1">Tunas Bertumbuh</h6>
								<p className='text-start'>
									Bekerja dalam posisi <i>Web Developer</i>, membangun situs utama dari Tunas Bertumbuh.
								</p>
							</VerticalTimelineElement>
						</VerticalTimeline>
					</div>
					{/* <div className="bg-image-blue mt-4">
						<HomeEnd/>
					</div> */}
					
					<Zoom left>
						<div className="container-fluid">
							<div className="row">
								<div className="col-lg d-flex justify-content-start align-items-center">
									<img src={require("./imgs/talk.png")} alt="Vue" id='f1' style={{ width:"80%", height:"auto" }}/>
								</div>
								<div className="col-lg-7 d-flex justify-content-center align-items-center">
									<div>
										<h2 className='text-start fw-bold'>Let's talk !</h2>
										<p className='text-start my-0'>Mari berbicara, bekerja bersama. Berikut merupakan kontak saya yang dapat anda hubungi secara langsung.</p>
										<div className="row mt-3">
											<div className="col-lg p-3 d-flex justify-content-center align-items-center">
												<div>
													<img src={require("./imgs/029-instagram.png")} alt="Vue" id='f1' style={{ width:"40%", height:"auto" }}/>
													<p className='mb-0 mt-3'>@daxcella</p>
												</div>
											</div>
											<div className="col-lg p-3 d-flex justify-content-center align-items-center">
												<div>
													<img src={require("./imgs/008-twitter.png")} alt="Vue" id='f1' style={{ width:"40%", height:"auto" }}/>
													<p className='mb-0 mt-3'>@daxcella</p>
												</div>
											</div>
											<div className="col-lg p-3 d-flex justify-content-center align-items-center">
												<div>
													<img src={require("./imgs/027-linkedin.png")} alt="Vue" id='f1' style={{ width:"40%", height:"auto" }}/>
													<p className='mb-0 mt-3'>Daniel Axcella Kurniawan</p>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</Zoom>
				</div>
				
				<Footer/>
			</div>
		)
	}
}
